import React, {Component} from 'react';
import ValidateUser from '../../services/authentication/ValidateUser';
import logo from '../../assets/images/logo-inverse.svg';

/**
 * @class AppHeader
 * @extends {Component}
 * @description Normal user header
 */
class AppHeader extends Component {
    /**
     * constructor
     */
    constructor() {
        super();
        this.state = {
            username: null,
        };
    }

    /**
     * @class AppHeader
     * @extends {Component}
     * @description Normal user header
     */
    componentWillMount() {

        ValidateUser.getUserDetails().then((response) => {
            this.setState(() => {
                return {
                    username: response.username,
                };
            });
        });

    }

    /**
     * @class AppHeader
     * @extends {Component}
     * @description Normal user header
     */
    render() {
        return (
            <nav className="navbar navbar-inverse" style={{backgroundColor: "#000000"}}>
                <div className="container-fluid">
                    <div className="navbar-header">
                        <img id="logo" style={{height: '40px'}} src={logo} alt="wso2"/>
                        <a className="navbar-brand" href="#" style={{color:"#FBFCFC"}}>License Manager</a>
                    </div>
                    <ul className="nav navbar-nav navbar-right">
                        {/*<li><a>{this.state.username}</a></li>*/}
                        <li ><a style={{color:"#FBFCFC"}}>Pamoda</a></li>

                    </ul>
                </div>
            </nav>
        );
    }
}

export default AppHeader;



